textarea,
input {
  width: 100%;
  padding: 5px;
  border: 1px solid $colour-mid-grey;
  color: $colour-black;
  font-family: $font-body;

  &::-webkit-input-placeholder {
    font-family: $font-body;
  }
  &:-moz-placeholder {
    font-family: $font-body;
  }
  &::-moz-placeholder {
    font-family: $font-body;
  }
  &:-ms-input-placeholder {
    font-family: $font-body;
  }
}

select {
  background: none;
  border: 1px solid $colour-mid-grey;
  width: 100%;
  color: $colour-black;
  padding: 5px;
  font-family: $font-body;
}
