.select-container {
  position: relative;

  &::after {
    @include icon-font('\eb73');
    position: absolute;
    top: 5px;
    right: 5px;
    pointer-events: none;
  }

  select {
    appearance: none;
    padding: 5px 10px 5px 5px; // Allow room for the drop down icon
  }

  select::-ms-expand {
    display: none;
  }
}
