.application-container {
  flex: 1 0 auto;
  min-height: 0;
  padding: 30px;

  &:not(.expanded) { // This class will be applied when the application is displaying preview pins
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  .get-started {
    font-size: 1.5em;
    color: $colour-grey;
  }

  .pins-container {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
}
