/**
 * Animation to rotate the loading spinner on pin previews
 */
@keyframes rotate40 {
  0% {
    transform: rotate(0deg); }
  12.5% {
    transform: rotate(45deg); }
  25% {
    transform: rotate(90deg); }
  37.5% {
    transform: rotate(135deg); }
