/**
 * Page header component styling
 */
.page-header {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: $colour-pinterest-red;
  color: $colour-white;
  max-height: 10vh;
  height: 100%;
  padding: 0 30px;
  text-align: center;
  transition: max-height 300ms linear, justify-content 300ms linear;
  flex: 0 0 auto;

  &.large {
    max-height: 60vh;
    justify-content: center;
    flex-direction: column;
    transition: max-height 300ms linear, justify-content 300ms linear;

    .application-name {
      margin-bottom: 20px;
    }

    .authentication-container,
    .image-select-container {
      padding-top: 100px;
    }

    .description {
      display: block;
    }
  }

  .authentication-container,
  .image-select-container {
    text-align: center;
  }

  .description {
    display: none;
  }
}
