.action-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .refresh-boards {
    @include button-default('small');

    // The following is required before the Balloon.css library isn't working properly for the tooltip on this particular button
    &::before {
      top: -7px
    }

    &::after {
      top: -33px;
      left: 0;
    }
  }

  .send-all-to-board {
    display: flex;
    align-items: center;

    .select-container {
      padding-left: 10px;
    }
  }

  .send-to-pinterest {
    @include button-red('large');
    float: right;
    padding-left: 20px;
  }
}
