/**
 * Button mixin
 *
 * @param string $size The size of the button. Options are 'large' or 'small'. Default is 'large'
 */
@mixin button($size) {
  border: 0;
  display: inline-block;
  outline: none;
  box-shadow: none;
  cursor: pointer;
  text-align: center;
  font-family: $font-heading;

  @if $size == 'small' {
    padding: 5px 10px;
    font-size: 15px;
    //border-radius: 3px;
  } @else {
    padding: 10px 30px;
    font-size: 25px;
    //border-radius: 5px;
  }
}

@mixin button-default($size: 'large') {
  @include button($size);
  background-color: $colour-white;
  color: $colour-black;
}

@mixin button-white($size: 'large') {
  @include button($size);
  background-color: $colour-white;
  color: $colour-pinterest-red;
}

@mixin button-red($size: 'large') {
  @include button($size);
  background-color: $colour-pinterest-red;
  color: $colour-white;
}

@mixin button-blue($size: 'large') {
  @include button($size);
  background-color: $colour-blue;
  color: $colour-white;
}
