/**
 * Pin preview component styling
 */
.preview-container {
  position: relative;
  background: $colour-white;
  padding: 10px;
  margin-top: 30px;

  .preview-image-container {
    max-width: 255px;
    max-height: 250px;
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    align-items: center;
    background: url('../img/loader.svg') center center no-repeat;

    img {
      visibility: hidden;
      width: 100%;

      &.loaded {
        visibility: visible;
      }
    }
  }

  .board-selection {
    padding: 5px 0 0;
  }

  .note-container {
    padding: 5px 0;

    .note {
      &.invalid {
        border: 1px solid $colour-pinterest-red;

        &:focus,
        &:active {
          outline-color: $colour-pinterest-red;
        }
      }
    }
  }

  .image-uploading-container {
    display: none;
  }

  &.sending {
    .image-uploading-container {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: rgba(255,255,255,0.5);
      display: flex;
      align-items: center;
      justify-content: center;

      .loading-animation {
        animation: rotate40 1s infinite step-end;
        font-size: 50px;
        color: $colour-pinterest-red;
      }
    }
  }

  .uploaded-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0;
    position: absolute;
    top: -10px;
    right: -10px;
    color: $colour-green;
    transition: font-size 100ms linear;
    width: 30px;
    height: 30px;
  }

  &[data-pinned] {
    .uploaded-icon {
      font-size: 30px;
    }
  }
}
